.About-h2{
    color: darkblue;
    margin-left: 5rem;
    margin-bottom: 0px;
    margin-top: 0px;
    padding-top: 0px;
    font-size: 50px;
}

.About-img {
    height:400px;
    width: 400px;
    clear: both;
    float: left;
    margin: 10px;
}

.About-column {
    float: left;
    width: 40%;
    margin-left: 25px;
    margin-right: 0px;
    margin-top: 25px;
    padding: 10px;   
  }

.About-text {
    font-size: 30px;
}

.About-container {
    padding: 1rem;
    margin: 1rem;
}
.row:after {
    content: "";
    display: table;
    clear: both;
  }

  @media screen and (max-width: 768px) {
    .About-column {width: 50%;}
    .About-img {height: 300px;
                     width: 300px;}
    .About-text {font-size: 30px;
           text-align: initial;
           padding-left: 10px;}
    .github {font-size: 23px;
}
  }
  
@media screen and (max-width: 400px){
    .name {font-size: 35px;
           text-align: center;}
    .navbar-nav {display: inline;
                 justify-content: space-between;
                 justify-content: flex-end;}
    .nav-link {font-size: 15px;}
    .column {width: 100%;}
    .rounded-circle {height: 350;
                     width: 350px;}
    .text {font-size: 20px;
           text-align: initial;
           padding-left: 10px;}
    .github {font-size: 20px;
             text-align: center;}
}
